import React from "react"
// import { Styled } from "./ViewWord.styled"
import Search from "../../components/Search"
import { Link } from "gatsby"
import { NLPThesaurusLogo } from "src/components/Header/Header"
import { DomainsHomeStyled } from "../Domains/DomainsHome.styled"
import { faBolt } from "@fortawesome/pro-solid-svg-icons"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"

export default function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  let oxfordExample = function () {
    let text = `[
    ["bright","full of light","well lit","well lighted", ...],
    ["light-coloured","light-toned","pale", ...],
    ["easy to lift","not heavy","weighing very little","lightweight", ...],
    ["flimsy","lightweight","insubstantial","thin", ...]
]`
    return { __html: text }
  }
  /*
   * Variables
   */
  // for content
  let { word_input } = that.props
  return (
    <DomainsHomeStyled>
      <Search
        {...that.props}
        className={!word_input ? "Search WordHome" : "Search"}
        domains={false}
        title="Find another word for..." // // {[<div key="logo">{NLPThesaurusLogo}</div>, <div key="title">Another word for...</div>]}
        placeholder="..."
        home={true}
        cue={[
          <div key="1">
            The most reliable thesaurus: synonyms, roots, parts of speech, plurals, abbreviations, and sentiment.&nbsp;
          </div>,
          <span key="2" className="nowrap">
            Built for <span className="nowrap">AI applications</span>:{" "}
          </span>,
          <span key="3" className="nowrap">
            &nbsp; <FA icon={faBolt} className="color-accent" /> Powers our{" "}
            <Link to="/domain">domain name generator</Link>.
          </span>
        ]}
      />

      <div className="container">
        <div className="background">
          <article className="content">
            <h3 className="attention">No other thesaurus has so many entries!</h3>
            <p>
              Type in any word, like <Link to="/word?str=aloha">aloha</Link>, or{" "}
              <Link to="/word?str=aggregate">aggregate</Link> or <Link to="/word?str=appendectomy">appendectomy</Link>.
              Or <Link to="/word?str=zig">zig</Link> or <Link to="/word?str=zag">zag</Link>. <br />
              Even short phrases like <Link to="/word?str=water under the bridge">water under the bridge</Link> or{" "}
              <Link to="/word?str=in a while">in a while</Link>.
            </p>

            <h3 className="attention">The "best" thesaurus?</h3>

            <h4 className="attention">For some applications, "yes": </h4>
            <p>
              <b>1) All English words are covered</b>, and many short phrases.
              <br /> Other thesauruses will return results for only common words. Ours even includes prepositions,
              determiners, contractions, and other "auxillary" words which don't have their own meaning.
            </p>
            <p>
              <b>2) Sentiment analysis and cultural sensitivity built in</b>
              <br />
              We detect if a word might have negative connotation. You can choose to omit such words from your synonyms.
              The word "black" for example, has many negative synonyms like "evil" or "melancholy". We can omit these.
              For the most culturally sensitive or contradictory words, we've manually added many positive synonyms from
              every day language, instead of from a dictionary.
            </p>
            <p>
              <b>3) Word info</b>
              <br />
              Most accurate "root word" extraction. Singular, plural, abbreviation, acronym, bound-morpheme, etc... Even
              detects if the word is a Proper (Capitalized) name or place.
            </p>
            <p>
              <b>4) Parts of speech</b>
              <br />
              Reliably tell the first, second, third part of speech (noun, adjective, interjection, conjunction, etc.).
              For example, "light" is primarily a noun, then adjective, then verb. The word "they" is a pronoun, but
              also a determiner.
            </p>
            <p>
              <b>5) Most natural-sounding synonyms that a person might use</b>
              <br />
              Not "dictionary" style phrases and obscure words, but short conversational words. Our algorithms have
              found the shortest and most commonly used words, and placed them towards the front of the list. So, you
              can enhance your conversational AI, or just trust that our synonyms will be culturally accurate.
            </p>
            <hr />

            <h4 className="attention">And "no" for other applications: </h4>
            <p>
              We do not yet separate our synonyms into "senses". For each "part of speech", we return just one list of
              synonyms. Our flat list has the advantage of letting you know which synonyms overall are most important.
              Also it was much easier to develop.{" "}
            </p>
            <p>
              A more established thesaurus like Thesaurus.com, WordHippo, or Oxford group synonyms into "senses". So,
              they give you multiple "meanings" within the same part of speech. We only give you one meaning (sense) for
              each part of speech. Most words have only one meaning per part of speech. Having only one meaning per part
              of speech is much easier to work with, but when constructing phases can lead to confusion.
            </p>
            <p>
              <b>For example, for the word "light",</b>
              <br />a thesaurus with "senses" like Oxford will return several lists of <i>adjectives</i>:
              <br />
              {/* prettier-ignore */}
              <code dangerouslySetInnerHTML={oxfordExample()} />
            </p>
            <p>
              Our thesaurus returns only a single list of words for <i>adjectives</i>:{/* prettier-ignore */}
              <code>
                ["sunny", "lit", "bright", "glowing", "airy", "shining", "easy", "illuminated", "casual", "clean", "lightsome", "gleaming", "lighted", "fluorescent", ...]
              </code>
            </p>
            <p>
              A saving grace that we've found, at least for our domain suggestions, is that... when a word does have
              very distinct and contradictory meanings within the same part of speech... then one meaning is often
              positive and the other is negative. This is great, because we do separate our synonyms based on
              positive/negative sentiment. So, in a simplified way, this does sometimes lets us have 2 "senses" per part
              of speech.
            </p>
            <p>
              Anyway, we're considering adding this functionality soon. Just need to analyze all the synonyms of each
              synonym, and group the words together by meaning. Data science! If you like our thesaurus, but this
              feature is important to you, please let us know! It is possible. Just requires some work.
            </p>
            <hr />
            <p>More info and tools coming soon... </p>
            <hr />
          </article>
        </div>
      </div>
    </DomainsHomeStyled>
  )
}
